a:link, a:active {
  color: #639dff;
  text-decoration: none;
}

.btn.btn-primary {
  background-color: #3d77d9;
  border-color: #3d77d9;
}
.btn.btn-primary:hover {
  background-color: #255ebe;
  border-color: #255ebe;
}

.cta-btn-shadow {
  box-shadow: 0px 10px 10px rgba(98, 155, 255, 0.2);
}

.form-check-input:checked {
  background-color: #639dff;
  border-color: #639dff;
}